*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
/*app*/
.App{
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}






/*Sidebar*/

.Sidebar{
    width: 300px;
    min-width: 210px;
    height: 100%;
   
}
.Main{
    width: 100%;
    height: 100%;
    background-image: url('./images/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  
}

/*User*/

.User{
    display: flex;
    
    align-items: center;
    justify-content: space-around;
    padding: 1rem;
    border: 1px solid #ebebeb;

}
.User .logo img{
    width: 50px;
    border-radius: 50%;
    border: 1px solid black;

}

.User .Info p{
    font-size: 2rem;
    font-weight: bold;
}

.User .Info a{
    text-decoration: none;
    display: flex;
    justify-content: flex-end;
}

/*Modal*/
.Modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0, 0, 0.8);
    z-index: 99;
}

.Modal .container{
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -25%);
   
}


/*AddnewTodo*/

.AddNewTodo{
    border-bottom: 1px solid #ebebeb;
}

.AddNewTodo .btn{
    padding: 1rem;
    width: 100%;
    height: 70px;
}
.AddNewTodo .btn button{
    width: 100%;
    height: 100%;
    border: none;
    font-size: 1.5rem;
    background-color: #0000ff;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.AddNewTodo .btn button:hover{
    filter: brightness(1.1);
    transition: all 300ms ease;
}


.TodoForm {
    padding: 1rem 1rem calc(1rem + 40px) 1rem;
    width: 100%;
    background: white;
    border-radius: 4px;
  }
  .TodoForm .text input {
    padding: 1rem;
    width: 100%;
    margin-bottom: 0.5rem;
    border: none;
    font-size: 1.2rem;
  }
  .TodoForm .text input:focus {
    outline: none;
    border-bottom: 1px solid #0080ff;
  }
  .TodoForm .remind {
    display: flex;
    align-items: center;
    padding: 0.5rem;
  }
  .TodoForm p {
    margin-left: 10px;
  }
  .TodoForm .title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  .TodoForm .pick-day {
    padding: 1.5rem 1.5rem 0 1.5rem;
  }
  .TodoForm .pick-time {
    padding: 1.5rem;
  }
  .TodoForm .pick-project {
    padding: 0.5rem;
  }
  .TodoForm .pick-project .projects {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 1rem 0 1rem;
  }
  .TodoForm .pick-project .projects .project {
    border: 1px solid #0080ff;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
    cursor: pointer;
  }
  .TodoForm .pick-project .projects .active {
    background: #0080ff;
    color: white;
  }
  .TodoForm .cancel {
    position: absolute;
    top: 0;
    right: 5px;
    cursor: pointer;
  }
  .TodoForm .confirm {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  .TodoForm .confirm button {
    width: 100%;
    height: 40px;
    background: #0080ff;
    color: white;
    font-size: 1.1rem;
    border: none;
    border-radius: 0 0 4px 4px;
    cursor: pointer;
  }
.Calendar{
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}

.Calendar .header{
    display: flex;
    align-items: center;
}
.Calendar .header .title{
    display: flex; 
    flex: 1;
}

.Calendar .header .title p{
    margin-left: 5px;
    font-size: 1.5rem;
    font-weight: bold;
}

.Calendar .header .btns span{
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Calendar .header .btns span:hover{
    background-color: #ebebeb;
    transition: all 300ms ease-in-out;
}

.Calendar .items {
    padding: 0.5rem;

}

.Calendar .items .item {
    padding: 0.15rem;
    margin: 5px;
    font-size: 1rem;
    cursor: pointer;
    font-size: 1.5rem;
}

/*Project*/

.Projects{
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}

.Projects .header{
    display: flex;
    align-items: center;
}

.Projects .header .title{
    display: flex;
    align-items: center;
    flex: 1;
}

.Projects .header .title p{
    margin-left: 5px;
    font-size: 1.5rem;
    font-weight: bold;
}

.Projects .header .btns{
    display: flex;
    align-items: center;
}

.Projects .header .btns span .edit{
    margin-left: 5px;
}

.Projects .header .btns span{
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Projects .header .btns span:hover{
    background-color: #ebebeb;
    transition: all 300ms ease-in-out;
}

.Projects .items{
    padding: 0.5rem;
   
}

/*Project*/

.Project{
    display: flex;
    align-items: center;
    padding: 0.25rem;
    margin: 1px;
    font-size: 1rem;
    margin-right: 0px;
    padding-right: opx;
}

.Project .name{
    cursor: pointer;
    word-break: break-all;
    font-size:25px ;
    padding-right:0px ;
}

.Project .btns{
    display: flex;
    align-items: center;
    margin-left: 60px
}
.Project .btns .total-todos{
    position: absolute;
    right: 0;
    background: #ebebeb;
    width: 25px;
    height: 25px;
    font-size: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;

}

.Project .btns .edit-delete{
    
    right: 0;
    display: flex;
    

}
.Project .btns .edit-delete span{
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 300ms ease-in-out;
}
.Project .btns .edit-delete span .edit:hover{
    background-color: #acffac;
    
}

.Project .btns .edit-delete span .delete:hover{
    background-color: #ffacac;
}

.AddNewTodo .add-button span{
    margin-right: 5px;
}

.ProjectForm{
    background-color: white;
    padding: 1rem;
    border-radius: 4px;
    position: relative;
    padding-bottom: 40px;
}

.ProjectForm input{
    width: 100%;
    height: 40px;
    margin: 15px 0;
    border: none;
    font-size: 1.2rem;
}

.ProjectForm input:focus{
    outline: none;
    border-bottom: 1px solid #0000ff;
}
.ProjectForm  button{
    position: absolute;
    bottom: 0;
    height: 40px;
    width: 50%;
    border: none;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;


}
.ProjectForm button.cancel{
    left: 0;
    background-color: #ebebeb;
    color: black;
}

.ProjectForm button.confirm{
    right: 0;
    background-color: #0000ff;
}

.ProjectForm button:hover{
    filter: brightness(1.1);
}

/*todos*/

.Todos{
    width: 450px;
    height: 80%;
    background-color: white;


    position: absolute;
    bottom: 40px;
    left: 300px;


    border-radius: 4px;
    opacity: 0.7;

    padding: 0 0.5rem 0.5rem 0.5rem;

}

.Todos .selected-project {
    margin: 1rem 0 1rem;
    text-transform: capitalize;
    font-size: 1.5rem;
    font-weight: bold;
    padding-bottom: 1px;
    border-bottom: 1px solid #ebebeb;
}

.Todos .Todos{
    overflow-y: auto;
}

/* Todo */
.Todo {
    padding: 0.5rem 1rem;
  }
  .Todo .todo-container {
    display: flex;
    align-items: center;
  }
  .Todo .todo-container span {
    cursor: pointer;
  }
  .Todo .todo-container .text {
    position: relative;
    flex: 1;
    margin: 0 10px;
    cursor: pointer;
  }
  .Todo .todo-container .text span {
    font-size: 0.8rem;
    color: #bebebe;
  }
  .Todo .todo-container .text .line {
    height: 1px;
    background: #bebebe;
    position: absolute;
    top: 25%;
    width: 0;
    transition: width 250ms ease-in-out;
  }
  .Todo .todo-container .text .line-through {
    width: 100%;
  }
  .Todo .todo-container .add-to-next-day {
    margin-right: 10px;
  }


  .EditTodo{
    position: absolute;
    bottom: 40px;
    left: 790px;

    background-color: white;

    height: 80%;
    width: 450px;


    border-radius: 4px;
    opacity: 0.7;
  }

  .EditTodo .header{
    margin: 1rem 0.5rem;

    font-size: 1.5rem;
    font-weight: bold;

    padding-bottom: 2px;
    border-bottom: 1px solid grey;

  }
  .EditTodo .container{
    padding-left: 10px;
  }
  .EditTodo .remind{
    margin-bottom: 5px;
  }
  .EditTodo .pick-time{
    margin: 7px;
  }
  .EditTodo .pick-day{
    margin: 7px;
  }
  .EditTodo .pick-project{
    margin-top: 5px;
  }
  .EditTodo .title p{
    font-size: 20px;
    font-weight: bold;
  }
  .EditTodo .TodoForm input{
    margin-top: 0px;
    padding-top: 0px;
  }

  /*Next 7days componet */
  .Next7Days .day{
    margin: 5px;
    display: flex;
  }
  .Next7Days .day .name{
    font-weight: bold;
    font-size: 1.2rem;
  }
  
  .Next7Days .day .total-todos{
    margin-left: 10px;
    font-weight: 200;

  }



































